      <div class="relative text-justify leading-tight mx-[5%] rounded-xl border-2 border-slate-400 bg-slate-100 px-8 pb-4 pt-6">
        Squadrons of 100 planes are sent out to battle and not all of them return. The ones that do return often have bullet marks on different sections of the planes. Can we use the bullet mark information to make the planes safer? By reinforcing different panels of the planes with additional armour, we hope that enemy bullets will do less severe damage and more planes will return safely home. But keep in mind that adding too much extra armour will slow the planes down and make them easier targets.
        <div class="absolute text-white text-center -translate-y-1/2 -translate-x-1/2 top-0 left-1/2 w-fit text-xl font-bold bg-slate-500 min-w-[280px] py-1.5 rounded-lg mx-auto">Making War Planes Safer</div>
      </div>
    </div>
    <div class="md:flex w-full my-6">
      <div class="w-full md:w-2/5 p-4">
        <div class="flex w-full border-b border-slate-200 mb-4">
          <div v-for="m in Object.keys(modes)" class="text-center w-1/3 cursor-pointer" :class="{'border-b-2': currentMode === m}" :style="{borderColor: modes[m].colour}" @click="currentMode = m">
            <div>
              <i :class="`${currentMode === m ? 'fas' : 'fal'} ${modes[m].icon}`" :style="{color: currentMode === m ? modes[m].colour : '#ccc'}" class="text-3xl" />
            </div>
            <div class="text-sm -mt-1" :class="currentMode === m ? 'text-primary-darker' : 'text-gray-400'">{{ m }}</div>
          </div>
        </div>

        <div class="text-sm text-center leading-tight mb-2 mx-4 px-3 py-2 rounded-full" :style="{backgroundColor: modes[currentMode].colour+'1a'}">The planes have a base level of armour on them already. These additions are adding to the existing reinforcement.</div>
        <div v-for="loc in planeLocations" class="w-full">
          <div v-if="loc.modes.includes(currentMode)" class="w-full flex justify-center mb-2">
            <label :for="loc.name" class="w-[100px] pl-1.5 mb-auto">{{ loc.display }}</label>
            <span class="w-[21px] text-right font-bold mb-auto" :style="{color: modes[currentMode].colour}">{{ inputs[loc.name] / 10 }}</span>
            <div class="grow ml-4 mr-1.5">
              <input :id="loc.name" min="0" :max="modes[currentMode].inputMax * 10" step="1" v-model="inputs[loc.name]" type="range" class="w-full" :class="modes[currentMode].accent" list="tickMarks">
              <datalist id="tickMarks">
                <option v-for="(n, index) in (modes[currentMode].inputMax + 1)" :value="index * 10" :label="index" />
              </datalist>
              <div class="w-full relative text-xs leading-tight h-3 -mt-2">
                <span class="absolute top-0 left-[4.5px]">0</span>
                <span class="absolute top-0" :class="currentMode=== 'Beginner' ? 'right-0.5' : 'right-[4.5px]'">{{ modes[currentMode].inputMax }}</span>
              </div>
            </div>
          </div>
        </div>

        <div class="text-center my-4">You're adding
          <span class="text-xl font-bold" :style="{color: modes[currentMode].colour}">{{ sumInputs }}</span> extra kilograms of armour to each plane.
        </div>

        <div class="flex justify-center mx-auto w-10/12 rounded border text-white font-bold overflow-hidden shadow-md active:shadow-sm" :style="{backgroundColor: modes[currentMode].colour}">
